import Vue from 'vue'
import VueRouter from 'vue-router'
import home from '../view/home.vue'
import user from '../view/user.vue'
import products from '../view/products.vue'
import page1 from '../view/page2.vue'
import page2 from '../view/page1.vue'
const routes = [
   {
       path:'/',
       name:'Main',
       component:()=>import('../view/Main.vue'),
       children:[
        {
            path:"/home",
            name:"home",
            component: home
        },
        {
            path:"/user",
            name:"user",
            component:user
        },
        {
            path:"/products",
            name:"products",
            component:products
        },
        {
            path:"/page1",
            name:"page1",
            component:page1
        },
        {
            path:"/page2",
            name:"page2",
            component:page2
        }
       ]
   }
]

Vue.use(VueRouter)

const router = new VueRouter({
    mode:"history",
    routes:routes
})

export default router